
import { Button, Input } from "antd"
import { Row } from "../../components/lib"
import { useSetUrlSearchParam } from "../../util/url"
import { useTasksSearchParams } from "./util"
import React, { useEffect, useState } from "react"
import { UserSelect } from "../../components/userSelect"
import { TaskTypeSelect } from "../../components/taskTypeSelect"
import { useDebounce } from "../../util"

export const SearchPanel = () => {
  const searchParams = useTasksSearchParams();
  const setSearchParams = useSetUrlSearchParam();
  // const [isComposing, setIsComposing] = useState(false); // 标记是否在输入法组合中

  console.log(searchParams);
  
   const [localName, setLocalName] = useState(searchParams.name || '');
   const debouncedName = useDebounce(localName, 500);
  // 防抖：500ms 后同步到 URL
  // 当防抖后的值变化时更新 URL
  useEffect(() => {
    setSearchParams({ name: debouncedName || undefined });
  }, [debouncedName]);
  

  const reset = () => {
    setSearchParams({
      typeId: undefined,
      processorId: undefined,
      tagId: undefined,
      name: undefined,
    });
  };

  return (
    <Row marginBottom={4} gap={true}>
      <Input
        style={{ width: "20rem" }}
        placeholder={"任务名"}
        value={localName}
        // onChange={(evt) => setSearchParams({ name: evt.target.value })}
         onChange={(e) => setLocalName(e.target.value)}

      />
      <UserSelect
        defaultOptionName={"经办人"}
        value={searchParams.processorId}
        onChange={(value) => setSearchParams({ processorId: value })}
      />
      <TaskTypeSelect
        defaultOptionName={"类型"}
        value={searchParams.typeId}
        onChange={(value) => setSearchParams({ typeId: value })}
      />
      <Button onClick={reset}>清除筛选器</Button>
    </Row>
  );
};
